// common mixins - start

@mixin qodefBckImageStyle() {
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
}

@mixin qodefImageOverlayHoverStyle($with-hover: true) {
    
    @if ($with-hover) {
        
        &:hover {
            
            &:after {
                opacity: 1;
            }
        }

        &:after {
            @include qodefAbsoluteHolderLayout();
            content: '';
            background-color: rgba($default-heading-color, .4);
            opacity: 0;
            transition: opacity .2s ease-in-out;
        }

    } @else {
        @include qodefAbsoluteHolderLayout();
        content: '';
        background-color: rgba($default-heading-color, .4);
        opacity: 0;
        transition: opacity .2s ease-in-out;
    }
}

@mixin qodefStandardPaginationStyle($list_type: null) {
    @include qodefRelativeHolderLayout(top);
    margin: 40px 0 0;
    clear: both;

    ul {
        @include qodefRelativeHolderLayout(top);
        padding: 0;
        margin: 0;
        list-style: none;
        text-align: left;

        li {
            position: relative;
            display: inline-block;
            vertical-align: top;
            margin: 0 6px;
            
            &:first-child {
                margin: 0 6px 0 0;
            }

            a {
                position: relative;
                display: inline-block;
                vertical-align: top;
                margin: 0;
                padding: 0 6px;
                font-size: 14px;
                font-weight: bold;
            }

            &.qodef-pag-active {
                
                a {
                    position: relative;
                    color: $first-main-color;
    
                    &:after {
                        content: '';
                        width: 100%;
                        height: 1px;
                        background-color: currentColor;
                        position: absolute;
                        bottom: 0;
                        left: 0;
                    }
                }
            }

            &.qodef-pag-prev,
            &.qodef-pag-next,
            &.qodef-pag-first,
            &.qodef-pag-last {
                margin: 0 2px;

                a {
                    font-size: 20px;

                    span {
                        display: block;
                        line-height: inherit;

                        &:before {
                            display: block;
                            line-height: inherit;
                        }
                    }
                }
            }
    
            &.qodef-pag-prev {
                margin-right: 15px;
            }
            &.qodef-pag-next {
                margin-left: 15px;
            }

            @if ($list_type == 'shortcode') {
                
                &.qodef-pag-prev {
                    
                    a {
                        opacity: 0;
                    }
                }

                &.qodef-pag-next {
                    
                    a {
                        opacity: 1;
                    }
                }

            } @else if ($list_type == 'shop') {
                span {
                    position: relative;
                    display: inline-block;
                    vertical-align: top;
                    margin: 0;
                    padding: 0;
                    color: $first-main-color;
                }

                a {
                    
                    &.next,
                    &.prev {
                        font-size: 0;
                        line-height: 0;

                        &:before {
                            display: block;
                            font-family: 'Ionicons'; // same icon pack as in our templates for pagination
                            font-size: 20px;
                            line-height: 26px;
                            font-weight: 400;
                            -webkit-font-smoothing: antialiased;
                            -moz-osx-font-smoothing: grayscale;
                        }
                    }

                    &.prev {
                        margin-right: 10px;

                        &:before {
                            content: "\f3cf";
                        }
                    }

                    &.next {
                        margin-left: 10px;

                        &:before {
                            content: "\f3d1";
                        }
                    }
                }
            }
        }
    }
}

@mixin qodefButtonDefaultStyle() {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    width: auto;
    margin: 0;
    font-family: inherit;
    outline: none;
    box-sizing: border-box;
    transition: color .2s ease-in-out, background-color .2s ease-in-out, border-color .2s ease-in-out;
}

@mixin qodefButtonSimpleStyle() {
    padding: 0;
    border: none;
    
    &:after {
        content: '';
        display: block;
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 1px;
        background-color: currentColor;
    }
}

@mixin qodefButtonSize($size: medium) {
    
    @if ($size == 'small') {
        padding: 8px 20px;
        font-size: 17px;

    } @else if ($size == 'medium') {
        padding: 11px 32px 12px;
        font-size: 20px;

    } @else if ($size == 'large') {
        padding: 17px 39px;
        font-size: 25px;

    } @else if ($size == 'huge') {
        display: block;
        width: 100%;
        text-align: center;
        padding: 17px 39px;
        font-size: 25px;
    }
}

@mixin qodefButtonTransparentColor() {
    color: $default-text-color;
    background-color: transparent;
}

@mixin qodefButtonSolidColor($important: null) {
    color: #fff $important;
    background-color: $default-heading-color $important;
    border: 1px solid transparent $important;
}

@mixin qodefButtonSolidHoverColor($important: null) {
    color: #fff $important;
    background-color: $first-main-color $important;
}

@mixin qodefButtonOutlineColor($important: null) {
    color: $default-heading-color $important;
    background-color: transparent $important;
    border: 1px solid #ccc $important;
}

@mixin qodefButtonOutlineHoverColor($important: null) {
    color: #fff $important;
    background-color: $first-main-color $important;
    border-color: transparent $important;
}

@mixin qodefButtonLineBellow($button-padding-left: null, $line-bottom-position: null) {
    &:after {
        content: '';
        display: block;
        position: absolute;
        left: $button-padding-left;
        bottom: $line-bottom-position;
        width: calc(100% - #{2*$button-padding-left});
        height: 1px;
        background-color: currentColor;
    }
}

@mixin qodefPlaceholder {
    &::-webkit-input-placeholder {
         @content
     }
    
    &:-moz-placeholder {
        @content;
        
        opacity: 1;
    }
    &::-moz-placeholder {
        @content;
        
        opacity: 1;
    }
    &:-ms-input-placeholder {
        @content
    }
}

// common mixins - end

