// mixins styles - start

%input-style {
    position: relative;
    width: 100%;
    margin: 0 0 #{$input-margin}px;
    padding: 10px 10px 3px 0;
    font-family: inherit;
    font-size: 19px;
    line-height: 1.05;
    font-weight: bold;
    color: $default-heading-color;
    background-color: transparent;
    border-width: 0;
    border-bottom: 2px solid $default-heading-color;
    border-radius: 0;
    outline: 0;
    cursor: pointer;
    -webkit-appearance: none;
    box-sizing: border-box;
    transition: border-color 0.2s ease-in-out;

    &:focus {
        color: $default-heading-color;
        border-color: $default-text-color;
    }

    @include qodefPlaceholder {
        color: inherit;
    }
}

// mixins styles - end

