// heading mixins - start

@mixin qodefH1() {
    font-size: 90px;
    line-height: 1.02em;
    font-weight: 700;
}

@mixin qodefH2() {
    font-size: 55px;
    line-height: 1.09em;
    font-weight: 700;
}

@mixin qodefH3() {
    font-size: 35px;
    line-height: 1.14em;
    font-weight: 700;
}

@mixin qodefH4() {
    font-size: 26px;
    line-height: 1.26em;
    font-weight: 700;
}

@mixin qodefH5() {
    font-size: 20px;
    line-height: 1.35em;
    font-weight: 700;
}

@mixin qodefH6() {
    font-size: 17px;
    line-height: 1.29em;
    font-weight: 700;
}

@mixin qodefHeadingStyle($with-heading: null, $with-color: true) {

    @if ($with-color) {
        color: $default-heading-color;
    }

    @if ($with-heading == 'h1') {
        @include qodefH1();
    } @else if ($with-heading == 'h2') {
        @include qodefH2();
    } @else if ($with-heading == 'h3') {
        @include qodefH3();
    } @else if ($with-heading == 'h4') {
        @include qodefH4();
    } @else if ($with-heading == 'h5') {
        @include qodefH5();
    } @else if ($with-heading == 'h6') {
        @include qodefH6();
    }
}

@mixin qodefDefaultHeadingStyle() {
    @include qodefHeadingStyle();
    font-family: $default-text-font;
    margin: 25px 0;

    -ms-word-wrap: break-word;
    word-wrap: break-word;

    a {
        @include qodefTypographyLayout();

        &:hover {
            color: $first-main-color;
        }
    }
}

@mixin qodefBody() {
    font-family: $default-text-font;
    font-size: 15px;
    font-weight: 400;
    line-height: 24px;
    color: $default-text-color;
    background-color: $default-background-color;
    -webkit-font-smoothing: antialiased;
}

// heading mixins - end

